@font-face {
  font-family: 'SUITE-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/SUITE-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}


*{
  font-family: 'SUITE-Regular';
  margin: 0px;
  padding: 0px;

}

header{
  display: flex;
  justify-content: center;
  padding: 20px 40px;
  box-sizing: border-box;
  width: 100%;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
}
button{
  position: fixed;
  bottom: 10%;
  transform: translate(-50%,0%);
  left: 50%;
  background-color: dodgerblue;
  color: white;
  font-size: 20px;
  border: 0px;
  border-radius: 300px;
  padding: 15px 30px;
  display: flex;
}
#check:checked ~ nav{
    right: 0%;
}
header nav{
  padding: 70px 0px;
  box-sizing: border-box;
  transition: 0.35s;
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 0%;
  right: -100%;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: steelblue;
}
header #close{
  top: 20px;
  right: 30px;
  position: absolute;
}
input[type="checkbox"]{
  display: none;
}
header a{
  font-size: 30px;
  margin-bottom: 10px;
  font-weight: bold;
  color: white;
  text-decoration: none;
  position: relative;
}


header img{
  width: 45px;
  height: 45px;
}